import {
  ChevronLeftIcon,
  ChevronRightIcon,
  DocumentTextIcon,
  LightBulbIcon,
} from "@heroicons/react/24/outline";
import { Button, message, Modal, Select, Skeleton } from "antd";
import * as React from "react";
import { HighLight, LoadBox } from "../../atoms";
import { IStatus } from "../../types";
import { fetchJSON } from "../../utils";
import GalleryDetailsView from "./details";

const GalleryView = () => {
  const prefixPath = process.env.GATSBY_PREFIX_PATH_VALUE;
  const [datasets, setDatasets] = React.useState<any | null>(null);
  const [model, setModel] = React.useState<any | string>("gpt-4-0314");
  const [error, setError] = React.useState<IStatus | null>(null);
  const [loading, setLoading] = React.useState<boolean>(false);
  const [selectedDataset, setSelectedDataset] = React.useState<number>(0);
  const [isModalOpen, setIsModalOpen] = React.useState(false);
  const [urlDatasetName, setUrlDatasetName] = React.useState<string | null>(
    null
  );
  const showModal = () => {
    setIsModalOpen(true);
  };

  const resetUrlState = () => {
    // console.log("resetting url state");
    window.history.pushState({}, "", "?");
  };

  const handleOk = () => {
    setIsModalOpen(false);
    resetUrlState();
  };

  const handleCancel = () => {
    setIsModalOpen(false);
    resetUrlState();
  };

  const fetchData = (
    url: string,
    payLoad: any,
    result: any,
    setResult: any
  ) => {
    setError(null);
    setLoading(true);

    const onSuccess = (data: any) => {
      if (data) {
        setResult(data);
        // console.log(data);
      }
      setLoading(false);
    };
    const onError = (err: any) => {
      setError(err);
      message.error(err);
      setLoading(false);
    };
    fetchJSON(url, payLoad, onSuccess, onError);
  };

  const dsets = (datasets && Object.keys(datasets)) || [];

  React.useEffect(() => {
    const datasetUrlParam = new URLSearchParams(window.location.search).get(
      "dataset"
    );
    const modelUrlParam = new URLSearchParams(window.location.search).get(
      "model"
    );
    if (datasetUrlParam && modelUrlParam) {
      // console.log("setting model and dataset");
      setUrlDatasetName(datasetUrlParam);
      setModel(modelUrlParam);
    }
  }, []);

  React.useEffect(() => {
    if (urlDatasetName && datasets) {
      const idx = dsets.indexOf(urlDatasetName);
      if (idx !== -1) {
        setSelectedDataset(idx);
      } else {
        message.error(`No dataset found with name ${urlDatasetName}`);
      }
      // console.log("urlDatasetName", urlDatasetName);
    }
  }, [urlDatasetName, datasets]);

  React.useEffect(() => {
    if (selectedDataset !== 0) {
      showModal();
    }
  }, [selectedDataset]);

  const dsListView = () => {
    const tileWidth = "145px";
    return dsets.map((d: string, i: number) => {
      const isSelected = selectedDataset === i;
      const ds = datasets[d];
      const img = ds.image
        ? `data:image/png;base64,${ds.image}`
        : `${prefixPath}/images/default.png`;

      return (
        <div
          role={"button"}
          onClick={() => {
            setSelectedDataset(i);
            if (i === 0 || i === selectedDataset) {
              showModal();
            }
            // add to browser history without reloading the page
            window.history.pushState(
              {},
              "",
              `?dataset=${ds.filename}&model=${ds.model}`
            );
          }}
          className={` duration-300 hover:border-accent       break-inside-avoid   }`}
          key={"dsrow" + i}
        >
          <img
            // style={{ width: tileWidth }}
            src={img}
            className="rounded-t object-fit bg-white p-2 border border-secondary border-b-0 "
            alt="chart"
          />
          <div
            // style={{ width: tileWidth }}
            className={`mb-2 p-2 duration-300   break-all  rounded-b text-sm duration-400 ${
              isSelected
                ? "bg-green-700 text-white "
                : "  bg-secondary text-primary "
            }`}
          >
            <span className="break-all">{ds.filename}</span>
          </div>
        </div>
      );
    });
  };

  const handleKeyDown = (event: KeyboardEvent) => {
    // console.log("event", event.key, selectedDataset);
    if (event.key === "ArrowRight") {
      if (datasets !== null && selectedDataset !== datasets.length - 1) {
        setSelectedDataset((prev) => prev! + 1);
      }
    } else if (event.key === "ArrowLeft") {
      if (datasets !== null && selectedDataset && selectedDataset !== 0) {
        setSelectedDataset((prev) => prev! - 1);
      }
    }
  };

  React.useEffect(() => {
    // console.log("datasets us kiaded");
    if (datasets !== null) {
      document.addEventListener("keydown", handleKeyDown);
      return () => {
        // console.log("removing...");
        document.removeEventListener("keydown", handleKeyDown);
      };
    }
  }, [datasets, selectedDataset]);

  React.useEffect(() => {
    const datasetsUrl = `${prefixPath}/gallery/${model}/datasets.json`;
    const payLoad = { method: "GET" };
    fetchData(datasetsUrl, payLoad, datasets, setDatasets);
  }, [model]);

  const currentDataset = datasets && datasets[dsets[selectedDataset]];

  const shareOnTwitter = () => {
    const url = `https://microsoft.github.io/lida/gallery/?dataset=${currentDataset?.filename}&model=${currentDataset?.model}`;
    const message =
      "View this visualization that was automatically created with LIDA, @vykthur";
    const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(
      message
    )}&url=${encodeURIComponent(url)}`;

    window.open(
      twitterUrl,
      "_blank",
      "noopener,noreferrer,width=800,height=600"
    );
  };

  return (
    <div className="mt-10 ">
      <div className="text-3xl   pt-4 mb-4 font-bold">
        Gallery
        <span className="text-base text-accent">
          {" "}
          {datasets?.length} dataset{datasets?.length !== 0 && "s"}
        </span>
      </div>
      <div className=" mt-4 ">
        Select a dataset to view data explorations (questions) and
        visualizations generated by LIDA across multiple visual grammars.{" "}
      </div>
      <div className="text-sm flex-1   mb-4">
        {" "}
        The visualizations are based on data from the{" "}
        <a
          target="_blank"
          rel="noopener noreferrer"
          aria-label="vega_datasets repo"
          className="inline-block text-accent duration-300 hover:text-accent hover:brightness-90 "
          href=" https://github.com/altair-viz/vega_datasets"
        >
          vega_datasets
        </a>{" "}
        repo and generations are based on the selected OpenAI model [{" "}
        <span className="font-semibold">{model}</span> ] .
      </div>

      <div className="mb-10">
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={`${prefixPath}/files/paper.pdf`}
          className="rounded-md bg-green-700 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:text-white hover:brightness-90 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-green-600"
        >
          <DocumentTextIcon className="h-5 w-5 inline-block mr-2" />
          Read the Paper
        </a>
      </div>
      <div className="flex gap-3 mt-2 mb-4">
        <div>
          <div className="text-xs mb-1">
            Select a model <span className="font-semibold">[{model}]</span>
          </div>
          {/* <Select
            style={{ width: 170 }}
            aria-label="Select a model"
            placeholder="Select a model"
            onChange={(value) => {
              setModel(value);
            }}
            defaultValue={model}
            value={model}
            options={[
              { value: "gpt-3.5-turbo-0301", label: "gpt-3.5-turbo" },
              { value: "gpt-4-0314", label: "gpt-4" },
            ]}
          />{" "} */}
          <select
            aria-label="Select a model"
            className="border myselect select-wrapper mt-1 p-2 px-2 rounded"
            name="cars"
            id="cars"
            onChange={(e) => {
              setModel(e.target.value);
            }}
            defaultValue={model}
          >
            <option value="gpt-3.5-turbo-0301">gpt-3.5-turbo</option>
            <option value="gpt-4-0314">gpt-4</option>
          </select>
        </div>
      </div>

      {loading && (
        <div className="mt-10 pb-10">
          <LoadBox
            subtitle={"loading gallery datasets ..."}
            className="text-accent"
          />
          <Skeleton className="mt-4" paragraph={{ rows: 12 }} />
        </div>
      )}
      {!loading && datasets && (
        <div className="w-full overflow-auto scroll pb-4">
          {/* <div className="inline-flex gap-3 flex-wrap ">{dsListView()}</div> */}
          <div className="columns-2 md:columns-3 lg:columns-4 xl:columns-5 ">
            {dsListView()}
          </div>
        </div>
      )}
      {selectedDataset !== null && datasets !== null && (
        <Modal
          title={
            <>
              <span className="capitalize">{currentDataset?.filename}</span>{" "}
              Chart
            </>
          }
          open={isModalOpen}
          onOk={handleOk}
          onCancel={handleCancel}
          width={1000}
          footer={[
            <Button key="close" onClick={handleCancel}>
              Close
            </Button>,
            <Button
              className="bg-green-700 text-white"
              key="twitter"
              type="primary"
              onClick={shareOnTwitter}
            >
              Share on Twitter
            </Button>,
          ]}
        >
          <div className="flex flex-row   items-center gap-4">
            <button
              onClick={() => {
                if (selectedDataset !== 0) {
                  setSelectedDataset(selectedDataset - 1);
                }
              }}
              disabled={selectedDataset === 0}
              className={`bg-secondary text-primary rounded-md py-2 px-4 font-medium focus:outline-none ${
                selectedDataset === 0 && " opacity-30 "
              }`}
            >
              <ChevronLeftIcon className="h-4 w-5 inline-block" /> Previous
            </button>
            <div className="flex-1 justify-center text-center">
              {selectedDataset + 1} / {dsets.length}
            </div>

            <button
              onClick={() => {
                if (selectedDataset !== datasets.length - 1) {
                  setSelectedDataset(selectedDataset + 1);
                }
              }}
              disabled={selectedDataset === datasets.length - 1}
              className={`bg-secondary text-primary rounded-md py-2 px-4 font-medium focus:outline-none ${
                selectedDataset === datasets.length - 1 && " opacity-30 "
              }`}
            >
              Next <ChevronRightIcon className="h-4 w-5 inline-block" />
            </button>
          </div>

          {/* <SecondaryButton onClick={shareOnTwitter}>
            Share on Twitter
          </SecondaryButton> */}

          <GalleryDetailsView details={currentDataset} />

          <div className="mt-10">
            {" "}
            <LightBulbIcon className="text-accent h-8, w-8 mr-2 float-left " />
            Note: The data summary for each dataset is derived from a rule-based
            respresentation of the data. This representation is then{" "}
            <HighLight>enriched</HighLight> using an LLM (adding dataset
            descriptions, field descriptions, semantic types) and is used for
            all downnstream tasks (goal generation, chart generation, chart
            editing, chart question answering). This summary is then used for
            all subsequent tasks - goal exploration, visualization generation,
            visualization editing.{" "}
          </div>
          <a
            target={"_blank"}
            rel="noopener noreferrer"
            href={`${prefixPath}/files/paper.pdf`}
            className="block mt-4 text-sm font-semibold leading-6 text-accent"
          >
            Learn more in the paper <span aria-hidden="true">→</span>
          </a>
        </Modal>
      )}
    </div>
  );
};

export default GalleryView;
