import { message, Radio, RadioChangeEvent, Skeleton } from "antd";
import { result } from "lodash";
import * as React from "react";
import { LoadBox } from "../../atoms";
import { IGoal } from "../../types";
import { fetchJSON, scrollToElement } from "../../utils";
import GalleryVizView from "./viz";

const GalleryDetailsView = ({ details }: { details: any | null }) => {
  const [results, setResults] = React.useState<any>(null);
  const [loading, setLoading] = React.useState<boolean>(false);
  const [selectedGoal, setSelectedGoal] = React.useState<number>(0);
  // const [selectedLibrary, setSelectedLibrary] = React.useState<string|null>(null);
  const [library, setLibrary] = React.useState<string>(details.libs[0]);

  const vizDivBoxRef = React.useRef<HTMLDivElement>(null);
  const prefixPath = process.env.GATSBY_PREFIX_PATH_VALUE;

  React.useEffect(() => {
    // console.log("details updated");
    if (details) {
      // console.log("details", details);
      const url = `${prefixPath}/gallery/${details.model}/${details.dir}/enrich/${library}/result.json`;
      const payLoad = { method: "GET" };
      fetchData(url, payLoad, results, setResults);
    }
  }, [details, library]);

  React.useEffect(() => {
    setSelectedGoal(0);
    if (results) {
      // console.log("results updated", results);
      scrollToElement(vizDivBoxRef);
    }
  }, [results]);

  const onLibChange = (e: RadioChangeEvent) => {
    // console.log("radio checked", e.target.value);
    setLibrary(e.target.value);
  };

  const libraryRadioList = details?.libs.map((lib: string, i: number) => {
    return (
      <Radio key={"radio" + lib + i} value={lib}>
        {lib}
      </Radio>
    );
  });

  const goalListView = results?.goals.map((goal: IGoal, i: number) => {
    const isActive = selectedGoal === i;
    return (
      <div
        onClick={() => {
          setSelectedGoal(i);
        }}
        role={"button"}
        key={"goal" + i}
        className={`bg-secondary duration-300 text-sm inline-block border rounded p-2 md:mb-2 ${
          isActive ? " border-accent " : " border-transparent "
        }`}
      >
        <div className="">{goal.question}</div>
      </div>
    );
  });

  React.useEffect(() => {
    console.log("component loaded");
  }, []);

  const fetchData = (
    url: string,
    payLoad: any,
    result: any,
    setResult: any
  ) => {
    setLoading(true);
    const onSuccess = (data: any) => {
      if (data) {
        setResult(data);
        // console.log(data);
      }
      setLoading(false);
    };
    const onError = (err: any) => {
      message.error(err);
      setLoading(false);
    };
    fetchJSON(url, payLoad, onSuccess, onError);
  };
  return (
    <div ref={vizDivBoxRef} className="mt-4 ">
      <div
        className={`  gap-3 ${loading ? "pointer-events-none opacity-10" : ""}`}
      >
        <div className="flex-1 text-left">
          <div className="font-semibold text-2xl">{results?.summary.name}</div>{" "}
          <div className="text-xs font-semibold">
            [
            {results?.summary.field_names?.map((name: string, i: number) => {
              const fields = results?.summary.field_names;
              return (
                <span key={"fname" + i}>
                  {" "}
                  {name} {!(i === fields.length - 1) && ","}{" "}
                </span>
              );
            })}
            ]
          </div>
        </div>
        <div className="mt-4">
          {" "}
          <div className="p-2 inline-block rounded bg-secondary">
            <Radio.Group onChange={onLibChange} value={library}>
              {libraryRadioList}
            </Radio.Group>
          </div>
        </div>
      </div>

      {
        <div className={`  relative gap-3 mt-2 duration-300 `}>
          {loading && (
            <div
              style={{ zIndex: 100 }}
              className="absolute top-0 left-0 right-0 bottom-0  text-accent"
            >
              <div className="flex justify-center items-center h-full">
                <LoadBox className=" " subtitle={"loading chart .."} />
              </div>
              {/* other elements in the parent div */}
            </div>
          )}
          <div className={`${loading ? "pointer-events-none opacity-10" : ""}`}>
            <div className="  ">
              <div className="text-sm mt-4">
                Generated data exploration goals. Select one!
              </div>
              <div className="mt-2 flex flex-wrap md:flex-nowrap md:inline-flex gap-3">
                {goalListView}
              </div>
            </div>

            <GalleryVizView
              library={library}
              goal={results?.goals[selectedGoal]}
              code={results?.code[selectedGoal][0]}
              raster={results?.chart[selectedGoal][0]?.raster}
              summary={results?.summary}
              error={results?.chart[selectedGoal][0]?.error}
              status={results?.chart[selectedGoal][0]?.status}
            />
          </div>
        </div>
      }
    </div>
  );
};
export default GalleryDetailsView;
