import {
  ChartPieIcon,
  ClipboardDocumentIcon,
  CodeBracketIcon,
  ExclamationTriangleIcon,
} from "@heroicons/react/24/outline";
import { Tabs } from "antd";
import * as React from "react";
import { IGoal } from "../../types";
import ChartRasterView from "../chartview/chartraster";
import { CodeBlock } from "../chartview/codeblock";

const GalleryVizView = ({
  code,
  raster,
  library,
  goal,
  summary,
  error,
  status,
}: {
  code: string;
  raster: string;
  library: string;
  goal: IGoal;
  summary: any;
  error: any;
  status: boolean;
}) => {
  const tabs = [
    {
      label: (
        <div>
          {" "}
          <CodeBracketIcon className="inline-block h-4" /> Python Code
        </div>
      ),
      key: "1",
      children: (
        <div className="w-full overflow-hidden">
          <CodeBlock
            code={code}
            language="python"
            wrapLines={true}
            maxHeight="380px"
          />
        </div>
      ),
    },
    {
      label: (
        <div>
          {" "}
          <ClipboardDocumentIcon className="inline-block h-4" /> Data Summary
        </div>
      ),
      key: "2",
      children: (
        <div className="w-full overflow-hidden">
          <CodeBlock
            code={JSON.stringify(summary, null, 2)}
            language="javascript"
            wrapLines={true}
            maxHeight="380px"
          />
        </div>
      ),
    },
  ];

  if (status === false && error) {
    tabs.push({
      label: (
        <div>
          {" "}
          <ExclamationTriangleIcon className="inline-block h-4 text-orange-400" />{" "}
          Error
        </div>
      ),
      key: "3",
      children: (
        <div className="w-full overflow-hidden">
          <div className="text-xs mb-2 text-primary mt-2 "> Error Message </div>
          <CodeBlock
            code={error.message}
            language="javascript"
            wrapLines={true}
          />
          <div className="text-xs mb-2 text-primary mt-2"> Traceback </div>
          <CodeBlock
            code={error.traceback}
            language="javascript"
            wrapLines={true}
          />
        </div>
      ),
    });
  }

  return (
    <div>
      <div className=" w-full mt-6 ">
        <div className="text-lg font-semibold">{goal?.question}</div>
        <div className="text-sm text-accent">{goal?.visualization}</div>
        <span className="text-sm">{goal?.rationale}</span>
      </div>
      <div className="md:flex gap-3">
        <div className="pt-4">
          <ChartRasterView raster={raster} library={library} goal={goal} />
        </div>
        <div className="flex-1">
          <Tabs defaultActiveKey="1" onChange={() => {}} items={tabs} />
        </div>
      </div>
    </div>
  );
};

export default GalleryVizView;
